.header {
    width: 100%;
    max-width: 1920px;
    margin: 0;
    height: 88px;
    padding: 0;
    position: relative;
    background: #1C1C21;
    box-shadow: 0px 24px 32px rgba(0, 0, 0, 0.04), 0px 16px 24px rgba(0, 0, 0, 0.04), 0px 4px 8px rgba(0, 0, 0, 0.04), 0px 0px 1px rgba(0, 0, 0, 0.04);
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    text-decoration: none;
}


@media screen and (max-width: 400px) {
    .header {
        width: 100%;
        position: fixed;
        bottom: 0px;
        height: 40px;
    }
}


.navigation {
    text-decoration: none;
    justify-content: center;
    align-items: center;
    display: flex;
    flex-direction: row;
    padding: 0;
    margin: auto;
    width: 100%;
}

.logo {
    margin: 0 200px 0 200px;
    padding: 0;
}

@media screen and (max-width: 400px) {
    .logo {
        visibility: hidden;
    }
}


.link {
    font-family: 'BlenderPro';
    text-decoration: none;
    list-style-type: none;
    padding: 0;
    margin: 0;
    font-style: normal;
    font-weight: 900;
    font-size: 20px;
    line-height: 19px;
    color: #8A8A8C;
    cursor: pointer;
    display: flex;
    gap: 10px;
    justify-content: center;
    align-items: center;
}

.link:hover {
    color: #A59557;
}

.link:hover * {
    color: #A59557;
    fill: #A59557;
}